import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class Cell extends React.Component {
    render() {
        return (<div className={"cell" + this.props.className}></div>)
    }
}
class Board extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            board: Array(this.props.width).fill(' ').map(
                (a, b) => b !== 0 && b !== this.props.width - 1
                ? Array(this.props.width).fill(' ').map(
                    (a, i) => i !== 0 && i !== this.props.width
                    ? (
                        Math.floor(Math.random() * 2) === 1
                        ? 'X'
                        : ' ')
                    : ' ')
                : Array(this.props.width).fill(' '))

        }
    }
    turnBornToAlive = currentBoard => {
        let length = currentBoard.length;
        for (let row = 1; row < length - 1; row++) {
            for (let col = 1; col < length - 1; col++) {
                if (currentBoard[row][col] === 'O') {
                    currentBoard[row][col] = 'X';
                } else if (currentBoard[row][col] === 'D') {
                    currentBoard[row][col] = ' ';
                }
            }
        }
        return currentBoard;
    }

    turnDeadToBorn = currentBoard => {
        let length = currentBoard.length;
        for (let row = 1; row < length - 1; row++) {
            for (let col = 1; col < length - 1; col++) {
                if (currentBoard[row][col] === " ") {
                    let surround = [
                        currentBoard[row - 1][col - 1],
                        currentBoard[row - 1][col],
                        currentBoard[row - 1][col + 1],
                        currentBoard[row][col - 1],
                        currentBoard[row][col + 1],
                        currentBoard[row + 1][col - 1],
                        currentBoard[row + 1][col],
                        currentBoard[row + 1][col + 1]
                    ];
                    let countNumberOfXnD = 0;
                    for (let i = 0; i < surround.length; i++) {
                        countNumberOfXnD += surround[i] === 'X' || surround[i] === 'D'
                            ? 1
                            : 0;
                    }
                    if (countNumberOfXnD === 3) {
                        currentBoard[row][col] = "O"
                    }
                }
            }
        }
        return currentBoard;
    }
    turnAliveToDead = currentBoard => {
        let length = currentBoard.length;
        for (let row = 1; row < length - 1; row++) {
            for (let col = 1; col < length - 1; col++) {
                if (currentBoard[row][col] === "X") {
                    let surround = [
                        currentBoard[row - 1][col - 1],
                        currentBoard[row - 1][col],
                        currentBoard[row - 1][col + 1],
                        currentBoard[row][col - 1],
                        currentBoard[row][col + 1],
                        currentBoard[row + 1][col - 1],
                        currentBoard[row + 1][col],
                        currentBoard[row + 1][col + 1]
                    ];
                    let countNumberOfAlive = 0;
                    for (let i = 0; i < surround.length; i++) {

                        countNumberOfAlive += surround[i] === 'X' || surround[i] === 'O' || surround[i] === 'D'
                            ? 1
                            : 0
                    }

                    if ([2, 3].indexOf(countNumberOfAlive) !== -1) {
                        currentBoard[row][col] = "X"
                    } else if ([
                        0,
                        1,
                        4,
                        5,
                        6,
                        7,
                        8
                    ].indexOf(countNumberOfAlive) !== -1) {
                        currentBoard[row][col] = "D"
                    }

                }
            }
        }
        return currentBoard;
    }
    generateBoard = (currentBoard) => {

        currentBoard = this.turnAliveToDead(currentBoard);
        currentBoard = this.turnDeadToBorn(currentBoard);
        currentBoard = this.turnBornToAlive(currentBoard);
        return currentBoard;

    }

    generateInitialColumnCell = (row) => {
        let array = this.state.board.slice()
        return array[row].slice().map((a, b) => {
            let noBorder = b !== 0 && b !== this.props.width - 1 && row !== 0 && row !== this.props.width - 1;
            if (noBorder) {
                if (a === "X") {
                    return <Cell className={" alive"} key={row * (this.props.width - 1) + b}/>
                } else if (a === "D") {
                    return <Cell className={" dead"} key={row * (this.props.width - 1) + b}/>
                } else if (a === "O") {
                    return <Cell className={" born"} key={row * (this.props.width - 1) + b}/>
                } else {
                    return <Cell className={""} key={row * (this.props.width - 1) + b}/>
                }
            }

        })
    }
    generateInitialRow = () => {

        let array = this.state.board.slice();
        if (this.props.generation === 0) {
            return array.slice().map((a, b) => {
                return (<div className="game-row" key={b}>
                    {this.generateInitialColumnCell(b)}
                </div>)
            });
        } else {

            return this.generateBoard(array).map((a, b) => {
                return (<div className="game-row" key={b}>
                    {this.generateInitialColumnCell(b)}
                </div>)
            });
        }

    }
    render() {

        return (<div>{this.generateInitialRow()}</div>);
    }
}

class Game extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            generation: 0
        }
    }
    timer = () => {
        this.setState({
            generation: this.state.generation + 1
        })
    }
    componentDidMount = () => {
        this.interval = setInterval(this.timer, 100);
    }
    componentWillUnmount = () => {
        clearInterval(this.interval)
    }
    render() {
        return (<div className="row">
            <div className="col-lg-12 col-xs-12">
                <h1>Game Of Life</h1>
                <h3 className="generation">Generation: {this.state.generation}</h3>
            </div>
            <div className="col-lg-12">

                <Board generation={this.state.generation} width={32}/>
            </div>
            <div className="col-lg-12 col-xs-12 text-center">
                <button onClick={() => {
                        this.componentDidMount()
                    }} className="btn btn-primary button">Play</button>

                <button onClick={() => {
                        this.componentWillUnmount()
                    }} className="btn btn-success button">Pause</button>
            </div>
        </div>)
    }
}
ReactDOM.render(<Game/>, document.getElementById('content'));



